.道具提示框{font-size:small;background:#fff;max-width:16rem;z-index:10;box-shadow:0 0 12px rgba(0,0,0,.3)}.装备信息-头部{position:relative}.装备信息-头部 .道具缩略图{top:.75rem;left:.5rem;position:absolute;font-size:1rem;box-shadow:0 2px 8px rgba(0,0,0,.3)}.装备信息-主体{padding:0 .5rem .5rem .5rem}.装备信息-名称{color:#fff;background:#1f1f1f;padding:.5rem;font-size:1rem;border-bottom:2px solid #eee}.装备信息-厂商{color:#2f54eb}.装备信息-名称,.装备信息-分类,.装备信息-厂商{padding-left:5rem}.装备信息-描述{color:#8c8c8c;padding:1em 0}.装备信息-词条{display:flex;margin-bottom:.25em;align-items:center;background:#eee;padding:.25em}.装备信息-词条.主属性{padding:.5em .25em;box-shadow:0 0 0px 1px #ccc;margin-bottom:.75em}.装备信息-词条 .属性图标{margin-right:.25em}.装备信息-词条 .值{font-weight:600;margin-left:auto;text-align:left;width:2.5em}.装备信息-套装{padding-top:.5em;margin-top:1em;border-top:1px solid #ddd;display:flex}.装备信息-套装 .图标{width:3em;height:3em;background-size:100% auto;background-repeat:no-repeat;background-position:top center;flex:none;margin-right:.5em}