.目标阶段-选项 {
  background-color: transparent;
  background-position: left center;
  background-size: auto 100%;
  padding: 0.75em 1em 2em 3em;
  border: none;
  margin: 0;
  min-width: 10.5em;

  &::after {
    top: 0;
    left: 0;
    right: 0;
    bottom: 1.25em;
    content: "";
    position: absolute;
    border: 2px solid transparent;
    border-image: none;
    border-right-width: 4px;
    height: auto;
    width: auto;
    background: transparent;
    display: block !important;
  }
  &:hover,
  &.已选中 {
    &::after {
      border-image: linear-gradient(to right, rgba(0, 0, 0, 0) 50%, rgba(255, 255, 255, 1)) 10;
    }
  }
}

.目标阶段-选项箭头 {
  top: 0.75em;
  transform: none;
}
