.道具星级 {
  display: block;

  .星星::before {
    display: none;
  }

  .星星 {
    width: 1em;
    height: 1em;
    margin-right: -4px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 100%;
    vertical-align: middle;

    &.金色 {
      background-image: url('../../资源/icon100_star_yellow.png');
    }

    &.紫色 {
      background-image: url('../../资源/icon100_star_pink.png');
    }
  }
}
